import {
    Component,
    EventEmitter,
    h,
    Host,
    Prop,
    Event,
    Method,
    Element,
    Watch,
} from '@stencil/core';

export interface Tab {
    id: string;
    title: string;
}

export type Layout = 'vertical' | 'horizontal';

/**
 * ### interface Tab
 * ```
 * {
 *   id: string;
 *   title: string;
 * }
 * ```
 * @category Navigation
 * @description A tabs component for switching between different content sections. Provides keyboard navigation and accessible tab panel management. It acts as a controller.
 *
 * @slot nav-content - Content to be placed after the tabs
 * @slot tab-list - Used to place your own at-tab components instead of using the 'tabs' prop
 * @slot tab-content - Used to place your own at-tab-content component
 */
@Component({
    tag: 'at-tabs',
    shadow: false,
})
export class AtTabs {
    @Element() el: HTMLElement;

    /**
     * List of tabs the selector will have
     */
    @Prop()
    tabs: Tab[] = [];

    /**
     * Layout of the tabs
     */
    @Prop()
    layout: Layout = 'horizontal';

    /**
     * hide navigation when you want to provide custom controls
     */
    @Prop()
    hide_nav: boolean = false;

    /**
     * Sets the current active tab
     */
    @Prop({ mutable: true })
    active_tab?: string;

    /**
     * If true, tabs will fill the width of the container
     * @default false
     */
    @Prop()
    fill?: boolean = false;

    /**
     * Emits the id of the tab when a new active_tab is set
     */
    @Event({ bubbles: false })
    atuiTabChange: EventEmitter<string>;

    tabEls: HTMLAtTabTriggerElement[] = [];
    tabManualAdded: { id: string; title: string }[] = [];

    componentDidRender() {
        this.tabEls = this.getTabsElements();
        requestAnimationFrame(() => {
            this.initializeTabs();
            this.addEventListenersToTabs();
            requestAnimationFrame(() => {
                this.updateIndicatorPosition();
            });
        });
    }
    initializeTabs() {
        this.tabEls.forEach((tabEl) => {
            this.tabManualAdded.push({
                id: tabEl.tab_id,
                title: tabEl.tab_title,
            });
            tabEl.layout = this.layout;
            if (!this.active_tab && tabEl.is_active) {
                this.active_tab = tabEl.tab_id;
            }
            tabEl.is_active =
                !!this.active_tab && tabEl.tab_id === this.active_tab;
            tabEl.fill = this.fill;
        });
    }

    addEventListenersToTabs() {
        this.tabEls.forEach((tabEl) => {
            tabEl.addEventListener('click', () => {
                this.handleTabChange(tabEl.tab_id);
            });
        });
    }

    getTabsElements() {
        const navContainer = this.el.querySelector(
            `#container-${this.tabsetId}`,
        );
        if (!navContainer) return [];
        return Array.from(
            navContainer.querySelectorAll('at-tab-trigger'),
        ) as HTMLAtTabTriggerElement[];
    }

    componentDidUpdate() {
        this.updateIndicatorPosition();
    }

    @Watch('active_tab')
    handleActiveTabChange(newValue: string) {
        const activeTab = this.tabs.find((tab) => tab.id === newValue);
        const activeManualtab = this.tabManualAdded.find(
            (tab) => tab.id === newValue,
        );
        if (activeTab) {
            this.atuiTabChange.emit(activeTab.id);
            this.updateTabActiveStates(newValue);
            requestAnimationFrame(() => {
                this.updateIndicatorPosition();
            });
        } else if (activeManualtab) {
            this.atuiTabChange.emit(activeManualtab.id);
            this.updateTabActiveStates(newValue);
            requestAnimationFrame(() => {
                this.updateIndicatorPosition();
            });
        }
    }

    updateTabActiveStates(newValue: string) {
        this.tabEls.forEach((tab) => {
            tab.is_active = tab.tab_id === newValue;
        });
    }

    private tabsetId = `tabset-${Math.random().toString(36).substring(2, 11)}`;

    private get indicatorRef(): HTMLElement {
        return this.el.querySelector(`#indicator-${this.tabsetId}`);
    }

    private get containerRef(): HTMLElement {
        return this.el.querySelector(`#container-${this.tabsetId}`);
    }

    handleTabChange(id: string): void {
        this.active_tab = id;
    }

    /**
     * Gets the current active tab
     * @returns The current active_tab value
     */
    @Method()
    async getActiveTab(): Promise<string> {
        return this.active_tab;
    }

    /**
     * Sets the current active tab
     * @param value - The tab id to set as active
     */
    @Method()
    async setActiveTab(value: string): Promise<void> {
        this.active_tab = value;
    }

    get tabOptions(): any {
        if (this.tabs) {
            return this.tabs.map((tab) => (
                <at-tab-trigger
                    tab_id={tab.id}
                    tab_title={tab.title}
                    layout={this.layout}
                    is_active={this.active_tab === tab.id}
                    fill={this.fill}
                    onClick={() => this.handleTabChange(tab.id)}
                ></at-tab-trigger>
            ));
        }
    }

    private updateIndicatorPosition() {
        if (!this.indicatorRef || !this.containerRef) return;

        const activeTab = this.el.querySelector(
            'at-tab-trigger[data-active="true"]',
        );

        if (activeTab) {
            const containerRect = this.containerRef.getBoundingClientRect();
            const buttonRect = activeTab.getBoundingClientRect();

            this.indicatorRef.style.width = `${buttonRect.width - 16}px`;
            this.indicatorRef.style.left = `${buttonRect.left - containerRect.left}px`;
            this.indicatorRef.style.opacity = '1';
        }
    }

    checkLayoutAndRender(): any {
        if (this.layout === 'horizontal') {
            return (
                <at-layout
                    class="flex flex-grow"
                    template="tabset"
                    data-name="navigation-top"
                >
                    {!this.hide_nav && (
                        <div slot="tabset-navigation">
                            <nav
                                role="tablist"
                                class="relative flex flex-wrap gap-2 border-b border-gray-200 px-2 text-center text-sm font-medium"
                                id={`container-${this.tabsetId}`}
                            >
                                {this.tabOptions}
                                <slot name="tab-list" />
                                <slot name="tab-nav" />

                                <div
                                    id={`indicator-${this.tabsetId}`}
                                    class="bg-active-foreground pointer-events-none absolute bottom-0 z-10 mx-8 h-2 rounded-sm transition-[left] duration-150 ease-in-out"
                                    style={{
                                        left: '0',
                                        opacity: '0',
                                    }}
                                    aria-hidden="true"
                                    role="presentation"
                                />
                            </nav>
                        </div>
                    )}

                    <div
                        role="tabpanel"
                        slot="tabset-content"
                    >
                        <slot name="tab-content" />
                    </div>
                </at-layout>
            );
        }

        if (this.layout === 'vertical') {
            return (
                <at-layout
                    class="flex flex-grow"
                    template="master-detail"
                    data-name="navigation-left"
                >
                    <div slot="master">
                        <nav
                            role="tablist"
                            class="flex flex-grow flex-col pr-16"
                            id={`container-${this.tabsetId}`}
                        >
                            {this.tabOptions}
                            <slot name="tab-list" />
                            <slot name="tab-nav" />
                        </nav>
                    </div>
                    <div
                        role="tabpanel"
                        slot="detail"
                        class="flex flex-grow flex-col"
                    >
                        <slot name="tab-content" />
                    </div>
                </at-layout>
            );
        }
    }

    render() {
        return <Host>{this.checkLayoutAndRender()}</Host>;
    }
}

/**
 * @category Form Controls
 * @description A tab selector component for switching between different content sections. Provides keyboard navigation and accessible tab panel management.
 */
